@import "color";

.contact {
    width: 100%;
    height: 90vh;
    background-color: $second;
    display: flex;
    justify-content: center;
    align-items: center;

    >main {
        background-color: $first;
        height: 40rem;
        width: 70%;
        padding: 3rem;
        border-radius: 0 200px 200px 0;

        >h1k {
            border-bottom: 2px solid $second;
            padding: 1rem;
            width: fit-content;
        }

        >form {
            margin: 5rem;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            >div {
                height: rem;
                display: flex;
                align-items: center;
                justify-content: flex-end;
                width: 60%;
                margin: 2rem;

                >label {
                    font: 400 1.2rem $text1;
                    width: 20%;
                    margin: 0 1rem;
                }

                >input {
                    align-self: stretch;
                    width: 80%;
                    padding: 1rem;
                    border-radius: 5px;
                    border: rgba(123, 123, 123, 0.6);
                    border: none;
                }
            }

            >button {
                margin: 3rem 0;
                padding: 0.7rem;
                border: none;
                border-radius: 5px;
                background-color: $third;
                color: $first;
                width: 200px;
                font: 500 1rem $text1;

                &:hover {
                    opacity: 0.9;
                }
            }
        }
    }
}