@import 'color';

footer {
    background-color: $first;
    height: 200px;
    width: 100%;
    padding: 2rem;
    color: $dark;
    // display: grid;
    // grid-template-columns: 1rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;

    >div {
        display: flex;
        flex-direction: column;
        justify-content: center;

        >h1 {
            font: 700 2rem $text1;
            text-transform: uppercase;
            color: $third;
        }

        >p {
            font: 500 1.2rem $text1;
        }

        >h5 {
            font: 500 2rem $text1;
            margin: 1rem 0;
            text-align: center; 
        }
    }

    div {
        display: flex;
        flex-direction: column;
        align-items: center;

        >a {
            color: $dark;
            font: 900 1rem $text2;

            &:hover {
                color: $third;
            }
        }
    }
}